import { Inject, Injectable, NgZone } from '@angular/core';
import {
  ContextHubGenericStoreService,
  ContextHubStoreConfigInterface,
  CustomerCategoryEnum,
} from '@telenet/ng-lib-page';
import { DataLayerService } from '@telenet/ng-lib-datalayer';
import { Builder } from '@telenet/ng-lib-shared';
import { CustomerStore } from '../entities/customer';
import { map } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class ContextHubInvoiceService extends ContextHubGenericStoreService<CustomerStore> {
  private static readonly STORE = 'customer';

  constructor(
    protected dataLayerService: DataLayerService,
    _ngZone: NgZone,
    @Inject('Window') window: Window
  ) {
    super(
      _ngZone,
      window,
      dataLayerService,
      Builder<ContextHubStoreConfigInterface<CustomerStore>>()
        .storeModel(new CustomerStore())
        .storeName(ContextHubInvoiceService.STORE)
        .serviceName('CustomerStoreService')
        .build()
    );
  }

  isBusinessCustomer() {
    return this.getStoreData().pipe(map((customerStore) => customerStore.category === CustomerCategoryEnum.BUSINESS));
  }
}
